  background-color: transparent;
  border-color: #d2d2d2;
}

[type="radio"]:disabled + span {
  color: #d2d2d2;
}

[type="radio"]:disabled:checked + span {
  color: #d2d2d2;
}

[type="checkbox"]:disabled:checked + span:not(.lever) {
